.bgimg {
  background-image: url("./images/background.jpeg");
  height: 100%;
  background-position: center;
  background-size: cover;
  position: relative;
  color: white;
  font-family: "Courier New", Courier, monospace;
  font-size: 25px;
}

.middle {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
}

hr {
  margin: auto;
  width: 40%;
}

#github {
  width: 90px;
  height: 90px;
  margin-top: 10px;
  padding: 5px;
}
#github:hover {
  background-color: #4298f5;
  border-radius: 50%;
}

#count {
  font-size: 30px;
}
